import * as React from "react";
import styled from 'styled-components';
import { FormattedMessage } from "react-intl";
import PageLayout from '../components/PageLayout/PageLayout';
import LinkCard from '../components/LinkCard/LinkCard';
import Ideology from '../components/Ideology/Ideology';
import SimpleLocalize from '../components/SimpleLocalize/SimpleLocalize';

const ImageWrapper = styled.div`
  width: 100%;
  height: 400px;
  display: flex;
  position: relative;
`;

const BGImage = styled.div`
  background-image: url(/images/main.jpg);
  width: 100%;
  height: 400px;
  background-repeat: no-repeat;
`;

const TitleWrapper = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  color: #fff;
  right: 28px;
  top: 50%;
  transform: translateY(-50%);
  text-align: right;
  @media (min-width: 1280px) {
    right: 8%;
  }
`

const Title = styled.span`
  font-size: 40px;
  font-weight: bold;
`;

const SubTitle = styled.span`
  font-size: 18px;
`;

const IndexPage = (props) => {
  return (
    <SimpleLocalize {...props}>
      <PageLayout pageTitle="Home" type="Main">
        <ImageWrapper>
          <BGImage />
          <TitleWrapper>
            <SubTitle>    </SubTitle>
            <Title></Title>
            <p>
              <FormattedMessage
                id="about-us"
                defaultMessage="About us"
              />
            </p>
          </TitleWrapper>
        </ImageWrapper>
        <LinkCard
          data={[
            { icon: 'factory', title: ' ', link: '/company/introduction' },
            { icon: 'timeline', title: '', link: '/company/history' },
            { icon: 'list', title: ' ', link: '/product/carrier' },
            { icon: 'map', title: ' ', link: '/contact' },
          ]}
        />
        <Ideology
          data={[
            { icon: 'manufacture', title: '', subTitle: '  ' },
            { icon: 'handShake', title: '', subTitle: '     ' },
            { icon: 'eco', title: '', subTitle: '  ' },
          ]}
        />
      </PageLayout>
    </SimpleLocalize>
  );
}

export default IndexPage;
